<template>
    <div class="amid-info">
        <p class="title">修改基本資料</p>
        <p class="sub-title">MEMBER PORFOLE</p>
        <div class="info-input">
            <ul>
                <li>
                    <p class="input-title">管理員帳號</p>
                    <el-input v-model="account" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">管理員名稱</p>
                    <el-input v-model="name" placeholder="請輸入"></el-input>
                </li>
            </ul>
            <ul>
                <li>
                    <p class="input-title">管理員密碼</p>
                    <el-input v-model="password" placeholder="請輸入" show-password></el-input>
                </li>
                <li>
                    <p class="input-title">確認密碼</p>
                    <el-input v-model="confirmpassword" placeholder="請輸入" show-password></el-input>
                </li>
            </ul>
                    
        </div>
        <div class="car-btn">修改資料</div>
    </div>
</template>

<script>
export default {
    name:'amidInfo',
    components: {
    },
    data() {
        return {
            account:'王曉明',
            name:'小明',
            password:'123456',
            confirmpassword:'123456',
        }
    }


}
</script>

<style lang='scss' scoped>
.amid-info {
    padding: 100px 0;
    text-align: center;
    max-width: 850px;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
    }
    .info-input {
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .input-title {
            color: #ef6d38;
            padding-left: 25px;
            margin-bottom: 10px;
            text-align: left;
        }
        ul {
            li {
                margin-top: 30px;
                padding:0 10px;
            }
        }
        @media screen and (max-width: 900px){  
            display: block;
        }
        
    }
}

</style>


